/**
 * Industry Research Page JavaScript
 *
 * This file contains the JavaScript for the Industry Research page.
 */

// Import components
import Accordion from '../components/accordion';
import Tabs from '../components/tabs';
import { getFromLocalStorage, saveToLocalStorage } from '../utils/storage';

/**
 * Initialize the page
 */
function initPage() {
  console.log('Industry Research page initialized');

  // Initialize accordion
  Accordion.init({
    triggerSelector: '.industry-accordion-header',
    contentSelector: '.industry-accordion-content',
    closeOthers: true,
    animateHeight: true
  });

  // Initialize tabs
  Tabs.init({
    tabsContainerSelector: '.tabs-container',
    tabButtonSelector: '.tab-button',
    tabPanelSelector: '.tab-panel',
    activeClass: 'active'
  });

  // Set up industry filter
  setupIndustryFilter();

  // Set up action plan checklist
  setupActionPlanChecklist();
}

/**
 * Set up industry filter
 */
function setupIndustryFilter() {
  const filterButtons = document.querySelectorAll('.filter-button');
  const industryCards = document.querySelectorAll('.industry-card');

  if (filterButtons.length > 0 && industryCards.length > 0) {
    filterButtons.forEach(button => {
      button.addEventListener('click', function() {
        const filter = this.getAttribute('data-filter');

        // Remove active class from all buttons
        filterButtons.forEach(btn => btn.classList.remove('active'));

        // Add active class to clicked button
        this.classList.add('active');

        // Filter industry cards
        industryCards.forEach(card => {
          if (filter === 'all') {
            card.style.display = 'block';
          } else {
            const categories = card.getAttribute('data-categories').split(' ');
            if (categories.includes(filter)) {
              card.style.display = 'block';
            } else {
              card.style.display = 'none';
            }
          }
        });
      });
    });

    // Activate first filter by default
    if (filterButtons[0]) {
      filterButtons[0].click();
    }
  }
}

/**
 * Set up action plan checklist
 */
function setupActionPlanChecklist() {
  const checkboxes = document.querySelectorAll('.action-step-checkbox');
  const progressBar = document.getElementById('checklist-progress-bar');
  const progressText = document.getElementById('checklist-progress-text');
  const resetButton = document.getElementById('reset-checklist');

  if (checkboxes.length > 0 && progressBar && progressText) {
    // Load saved progress
    loadChecklistProgress();

    // Add event listeners to checkboxes
    checkboxes.forEach(checkbox => {
      checkbox.addEventListener('change', function() {
        saveChecklistProgress();
        updateChecklistProgress();
      });
    });

    // Add event listener to reset button
    if (resetButton) {
      resetButton.addEventListener('click', function() {
        resetChecklistProgress();
      });
    }

    // Update progress display
    updateChecklistProgress();
  }
}

/**
 * Save checklist progress to localStorage
 */
function saveChecklistProgress() {
  const checkboxes = document.querySelectorAll('.action-step-checkbox');
  const progress = {};

  checkboxes.forEach(checkbox => {
    const stepId = checkbox.getAttribute('data-step-id');
    progress[stepId] = checkbox.checked;
  });

  saveToLocalStorage('industryResearchProgress', progress);
}

/**
 * Load checklist progress from localStorage
 */
function loadChecklistProgress() {
  const savedProgress = getFromLocalStorage('industryResearchProgress');

  if (savedProgress) {
    const checkboxes = document.querySelectorAll('.action-step-checkbox');

    checkboxes.forEach(checkbox => {
      const stepId = checkbox.getAttribute('data-step-id');
      if (savedProgress[stepId] !== undefined) {
        checkbox.checked = savedProgress[stepId];
      }
    });
  }
}

/**
 * Update checklist progress display
 */
function updateChecklistProgress() {
  const checkboxes = document.querySelectorAll('.action-step-checkbox');
  const progressBar = document.getElementById('checklist-progress-bar');
  const progressText = document.getElementById('checklist-progress-text');

  const totalSteps = checkboxes.length;
  let completedSteps = 0;

  checkboxes.forEach(checkbox => {
    if (checkbox.checked) {
      completedSteps++;
    }
  });

  const progressPercentage = totalSteps > 0 ? Math.round((completedSteps / totalSteps) * 100) : 0;

  if (progressBar) {
    progressBar.style.width = `${progressPercentage}%`;
  }

  if (progressText) {
    progressText.textContent = `${completedSteps} of ${totalSteps} steps completed (${progressPercentage}%)`;
  }
}

/**
 * Reset checklist progress
 */
function resetChecklistProgress() {
  const checkboxes = document.querySelectorAll('.action-step-checkbox');

  // Uncheck all checkboxes
  checkboxes.forEach(checkbox => {
    checkbox.checked = false;
  });

  // Clear saved progress
  saveToLocalStorage('industryResearchProgress', {});

  // Update progress display
  updateChecklistProgress();
}

// Initialize the page when DOM is ready
document.addEventListener('DOMContentLoaded', initPage);

export default {
  initPage
};
